@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta{
    width: 50rem;
    height: 3rem;
    margin-bottom: 1rem;
    text-align: center;

    @include m.flex;
    gap: .5rem;

    &.error{
        background-color: v.$rojo; 
        color: v.$blanco;
        font-weight: bold;
    }

    &.exito{
        background-color: v.$verde; 
        color: v.$blanco;
        font-weight: bold;
    }
}